import PropTypes from "prop-types";
import React, { Fragment, useState, useEffect } from "react";
import MetaTags from "react-meta-tags";
import Paginator from "react-hooks-paginator";
import { BreadcrumbsItem } from "react-breadcrumbs-dynamic";
import { connect } from "react-redux";
import { getSortedProducts } from "../../helpers/product";
import { getSortedCollections } from "../../helpers/collections";
import LayoutOne from "../../layouts/LayoutOne";
import Breadcrumb from "../../wrappers/breadcrumb/Breadcrumb";
import ShopTopbar from "../../wrappers/product/ShopTopbar";
import CollectionsTopbar from "../../wrappers/collection/CollectionsTopbar"
import ShopProducts from "../../wrappers/product/ShopProducts";
import ShopCollections from "../../wrappers/collection/ShopCollections";

const AllCollections = ({ location, collections }) => {
  const [layout, setLayout] = useState("grid three-column");
  const sortType = "";
  const sortValue = "";
  const [filterSortType, setFilterSortType] = useState("");
  const [filterSortValue, setFilterSortValue] = useState("");
  const [offset, setOffset] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const [currentData, setCurrentData] = useState([]);
  const [sortedCollections, setSortedCollections] = useState([]);

  const pageLimit = 15;
  const { pathname } = location;

  const getLayout = layout => {
    setLayout(layout);
  };

  const getFilterSortParams = (sortType, sortValue) => {
    setFilterSortType(sortType);
    setFilterSortValue(sortValue);
  };

  useEffect(() => {
    let sortedCollections = getSortedCollections(collections, sortType, sortValue);
    const filterSortedCollections = getSortedCollections(
      sortedCollections,
      filterSortType,
      filterSortValue
    );
    sortedCollections = filterSortedCollections;
    setSortedCollections(sortedCollections);
    setCurrentData(sortedCollections.slice(offset, offset + pageLimit));
  }, [offset, collections, sortType, sortValue, filterSortType, filterSortValue]);

  return (
    <Fragment>
      <MetaTags>
        <title>LG | All Collections | SVM Electronics</title>
        <meta
          name="description"
          content="Collection page of SVM LG."
        />
      </MetaTags>

      <BreadcrumbsItem to={process.env.PUBLIC_URL + "/"}>Home</BreadcrumbsItem>
      <BreadcrumbsItem to={process.env.PUBLIC_URL + pathname}>
        All Collections
      </BreadcrumbsItem>

      <LayoutOne headerTop="visible">
        {/* breadcrumb */}
        <Breadcrumb />

        <div className="shop-area pt-95 pb-100">
          <div className="container">
            <div className="row">
              <div className="col-lg-12">
                {/* shop topbar default */}
                <CollectionsTopbar
                  getLayout={getLayout}
                  getFilterSortParams={getFilterSortParams}
                  collectionCount={collections.length}
                  sortedCollectionCount={currentData.length}
                />

                {/* shop page content default */}
                
                <ShopCollections layout={layout} collections={currentData} />

                {/* shop collection pagination */}
                <div className="pro-pagination-style text-center mt-30">
                  <Paginator
                    totalRecords={sortedCollections.length}
                    pageLimit={pageLimit}
                    pageNeighbours={2}
                    setOffset={setOffset}
                    currentPage={currentPage}
                    setCurrentPage={setCurrentPage}
                    pageContainerClass="mb-0 mt-0"
                    pagePrevText=""
                    pageNextText=""
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </LayoutOne>
    </Fragment>
  );
};

AllCollections.propTypes = {
  location: PropTypes.object,
  collections: PropTypes.array
};

const mapStateToProps = state => {
  return {
    collections: state.collectionData.collections
  };
};

export default connect(mapStateToProps)(AllCollections);
